// Notifications (Friends Requests, Invites) List Generation
function addList(arr, invitesarr){
    
    document.querySelector(".notCompleted").innerHTML = "<h3>Friend Requests</h3>";

    document.querySelector(".Completed").innerHTML = "<h3>Invites</h3>";

    document.getElementById("Heading").innerHTML = "Notifications";

    arr.forEach(function(e){
        const notCompleted = document.querySelector('.notCompleted');
	    const Completed = document.querySelector('.Completed');

	    const newLi = document.createElement('li');
	    const checkBtn = document.createElement('button');
	    const delBtn = document.createElement('button');

	    checkBtn.innerHTML = '<i class="fa fa-check"></i>';
	    delBtn.innerHTML = '<i class="fa fa-trash"></i>';

        newLi.textContent = e;
		notCompleted.appendChild(newLi);
		newLi.appendChild(checkBtn);
		newLi.appendChild(delBtn);


        checkBtn.addEventListener('click', function(){
            const parent = this.parentNode;
            parent.remove();
            //Completed.appendChild(parent);
            checkBtn.style.display = 'none';
            const tag = parent.textContent;
            firebaseRef = firebase.database().ref("Users/"+username+"/Friends");
            firebaseRef.update({
                [tag]  : "1",
            });
            firebaseRef = firebase.database().ref("Users/"+username+"/Notifications/"+tag);
            firebaseRef.remove();

            firebaseRef = firebase.database().ref("Users/"+tag+"/Friends");
            firebaseRef.update({
                [username] : "1",
            });
        });
    
        delBtn.addEventListener('click', function(){
            const parent = this.parentNode;
            parent.remove();
            const tag = parent.textContent;
            firebaseRef = firebase.database().ref("Users/"+username+"/Notifications/"+tag);
            firebaseRef.remove();
        });
    });


    invitesarr.forEach(function(e){
        const notCompleted = document.querySelector('.notCompleted');
	    const Completed = document.querySelector('.Completed');

	    const newLi = document.createElement('li');
	    const checkBtn = document.createElement('button');
	    const delBtn = document.createElement('button');

	    checkBtn.innerHTML = '<i class="fa fa-check"></i>';
	    delBtn.innerHTML = '<i class="fa fa-trash"></i>';

        newLi.textContent = e;
		Completed.appendChild(newLi);
		newLi.appendChild(checkBtn);
		newLi.appendChild(delBtn);


        checkBtn.addEventListener('click', function(){
            const parent = this.parentNode;
            parent.remove();
            //Completed.appendChild(parent);
            var KeyforInvite = "";
            checkBtn.style.display = 'none';
            const tag = parent.textContent;
            firebaseRef = firebase.database().ref("Users/"+username+"/Invites/");
            firebaseRef.child(tag).on("value", function(data){
                valb = data.val();
                KeyforInvite = valb;
            });
            console.log(KeyforInvite);
            document.getElementById("check").checked = false;
            document.querySelector(".EnterKey").value = KeyforInvite;
            document.querySelector(".JoinGame").style.display = "inline";
            document.querySelector(".wrapper").style.display = "none";
            document.querySelector(".wrapper2").style.display = "none";
            document.querySelector(".wrapper3").style.display = "none";
            firebaseRef = firebase.database().ref("Users/"+username+"/Invites/"+tag);
            firebaseRef.remove();
        });
    
        delBtn.addEventListener('click', function(){
            const parent = this.parentNode;
            parent.remove();
            const tag = parent.textContent;
            firebaseRef = firebase.database().ref("Users/"+username+"/Invites/"+tag);
            firebaseRef.remove();
        });
    });

}



