function countUpMe(count)
{
  var div_by = 100,
      speed = Math.round(count / div_by),
      $display = $('#Me'),
      run_count = 1,
      int_speed = 24;
  
  var int = setInterval(function() {
    if(run_count < div_by){
      $display.text(speed * run_count);
      run_count++;
    } else if(parseInt($display.text()) < count) {
      var curr_count = parseInt($display.text()) + 1;
      $display.text(curr_count);
    } else {
      clearInterval(int);
    }
  }, int_speed);
}


function countUpEnemy(count)
{
  var div_by = 100,
      speed = Math.round(count / div_by),
      $display = $('#Enemy'),
      run_count = 1,
      int_speed = 24;
  
  var int = setInterval(function() {
    if(run_count < div_by){
      $display.text(speed * run_count);
      run_count++;
    } else if(parseInt($display.text()) < count) {
      var curr_count = parseInt($display.text()) + 1;
      $display.text(curr_count);
    } else {
      clearInterval(int);
    }
  }, int_speed);
}

