var xLink = "http://www.w3.org/1999/xlink",
  select = function(s) {
    return document.querySelector(s);
  },
  container = select('.container'),
  monitorScreen = select('.monitorScreen'),
  screenMask = select('.screenMask'),
  outer = select('.outer'),
  border = select('.border'),
  foot = select('.foot'),
  logo = select('.logo'),
  shine = select('.shine'),
  monitorGroup = select('.monitorGroup'),
  monitorSVG = select('.monitorSVG')

TweenMax.set(container, {
  position: 'absolute',
  top: '50%',
  left: '50%',
  xPercent: -50,
  yPercent: -50
})
TweenMax.set(shine, {
  x: -112
})

var tl = new TimelineMax({
  repeat: 0,
  delay: 0
});
tl.timeScale(1)

tl.from(outer, 3, {
    scale: 0,
    transformOrigin: '50% 50%',
    ease: Elastic.easeOut.config(1, 0.7)
  })
  .from(border, 3, {
    scale: 0,
    transformOrigin: '50% 50%',
    ease: Elastic.easeOut.config(0.3, 0.9)
  }, '-=2.8')
  .from([monitorScreen, screenMask], 3, {
    scale: 0,
    transformOrigin: '50% 50%',
    ease: Elastic.easeOut.config(1, 1)
  }, '-=2.3')
  .from(shine, 2, {
    alpha: 0,
    x: -500,
    ease: Elastic.easeOut.config(1, 1)
  }, '-=2.5')
  .from(foot, 2, {
    scale: 0,
    transformOrigin: '50% 0%',
    ease: Elastic.easeOut.config(1, 0.65)
  }, '-=3')

.from(logo, 2, {
  scale: 2,
  alpha: 0,
  transformOrigin: '50% 50%',
  ease: Elastic.easeOut.config(1, 0.5)
}, '-=3')

.from(monitorGroup, 3, {
  rotation: -45,
  transformOrigin: '50% 50%',
  ease: Elastic.easeOut.config(1, 0.5)
}, '-=' + tl.duration());

TweenMax.set('svg', {
  visibility: 'visible'
})

monitorSVG.addEventListener('click', function(e){
  tl.play(0);
})








